<template>
  <v-form v-model="localValid">
    <v-row>
      <v-col cols="7">
        <v-text-field
          v-model.trim="localOrg.organization_name"
          :rules="rules.required"
          label="Nome"
          placeholder="Insira o nome da organização"
          prepend-inner-icon="mdi-tag-text-outline"
          color="secondary"
          density="compact"
          variant="outlined"
          :readonly="readonly"
        ></v-text-field>
      </v-col>
      <v-col cols="5">
        <v-text-field
          v-model.trim="localOrg.organization_nif"
          :rules="rules.number"
          label="NIF"
          placeholder="Insira o NIF da organização"
          prepend-inner-icon="mdi-numeric"
          counter="9"
          color="secondary"
          density="compact"
          variant="outlined"
          :readonly="readonly"
        ></v-text-field>
      </v-col>
      <v-col cols="7">
        <v-text-field
          v-model.trim="localOrg.organization_email"
          :rules="rules.email"
          type="email"
          label="Email"
          placeholder="exemplo@email.com"
          prepend-inner-icon="mdi-email"
          color="secondary"
          density="compact"
          variant="outlined"
          :readonly="readonly"
        ></v-text-field>
      </v-col>
      <v-col cols="5">
        <v-text-field
          v-model.trim="localOrg.organization_phone"
          :rules="rules.number"
          label="Telefone"
          placeholder="900000000"
          prefix="+351"
          prepend-inner-icon="mdi-phone"
          counter="9"
          color="secondary"
          density="compact"
          variant="outlined"
          :readonly="readonly"
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-text-field
          v-model.trim="localOrg.organization_address"
          :rules="rules.required"
          label="Morada"
          placeholder="Insira a morada da organização"
          prepend-inner-icon="mdi-map-marker"
          color="secondary"
          density="compact"
          variant="outlined"
          :readonly="readonly"
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-text-field
          v-model.trim="localOrg.organization_city"
          :rules="rules.required"
          label="Cidade"
          placeholder="Insira a cidade da organização"
          prepend-inner-icon="mdi-city"
          color="secondary"
          density="compact"
          variant="outlined"
          :readonly="readonly"
        ></v-text-field>
      </v-col>
    </v-row>
  </v-form>
</template>

<script>
export default {
  name: "OrgForm",
  props: {
    organization: {
      type: Object,
    },
    formValid: {
      type: Boolean,
      required: false,
      default: false,
    },
    readonly: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  data() {
    return {
      rules: {
        required: [
          (v) => !!v || "Campo obrigatório",
          (v) => (v && v.length <= 50) || "Máximo de 50 caracteres",
        ],
        email: [
          (v) => !!v || "Campo obrigatório",
          (v) => /.+@.+\..+/.test(v) || "Email inválido",
        ],
        number: [
          (v) => !!v || "Campo obrigatório",
          (v) => (v && v.length === 9) || "Número inválido",
        ],
      },
      valid: false,
    };
  },
  computed: {
    localOrg: {
      get() {
        return this.organization;
      },
      set(value) {
        this.$emit("update:organization", value);
      },
    },
    localValid: {
      get() {
        return this.formValid;
      },
      set(value) {
        this.$emit("update:formValid", value);
      },
    },
  },
};
</script>

<style></style>
