<template>
  <v-container
    ><v-row>
      <v-col cols="12">
        <v-card>
          <v-card-title>Filtro</v-card-title>
          <v-card-text>
            <v-row>
              <v-col cols="2">
                <v-text-field
                  v-model="filter.name"
                  clearable
                  label="Nome"
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-text-field
                  v-model="filter.city"
                  clearable
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  label="Cidade"
                ></v-text-field>
              </v-col>
              <v-col cols="2">
                <v-autocomplete
                  clearable
                  v-model="filter.organizationtype_id"
                  :items="organizationtypes"
                  item-title="organizationtype_name"
                  item-value="organizationtype_id"
                  label="Tipo de Organização"
                  density="comfortable"
                  variant="underlined"
                  color="primary"
                  :loading="isLoadingTypes"
                ></v-autocomplete>
              </v-col>
              <v-col cols="2">
                <v-btn
                  color="secondary"
                  icon
                  variant="text"
                  :disabled="isLoadingOrgs"
                  @click="refreshOrgs()"
                >
                  <v-icon size="x-large">mdi-magnify</v-icon>
                </v-btn>
              </v-col>
              <v-spacer></v-spacer>
              <v-col v-if="accountStore.hasRole(['Administrador'])" cols="2">
                <AddOrg @refresh="refreshOrgs()"></AddOrg>
              </v-col>
            </v-row>
          </v-card-text> </v-card
      ></v-col>
    </v-row>
    <v-row>
      <v-col>
        <OrganizationsTable
          :organizations="organizations"
          :isLoadingOrgs="isLoadingOrgs"
          :hasMore="hasMore"
          @loadMore="getOrgs()"
          @refresh="refreshOrgs()"
        ></OrganizationsTable>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AddOrg from "@/components/Organizations/AddOrg.vue";
import OrganizationsTable from "@/components/Organizations/OrganizationsTable.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "OrganizationsView",
  components: {
    AddOrg,
    OrganizationsTable,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoadingOrgs: false,
      organizations: [],
      filter: {
        name: null,
        city: null,
        organizationtype_id: null,
      },
      limit: 12,
      offset: 0,
      hasMore: false,

      organizationtypes: [],
      isLoadingTypes: false,
    };
  },
  methods: {
    async getOrgs() {
      this.isLoadingOrgs = true;
      await axios
        .request({
          url: "api/organizations",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          params: {
            entity_name: this.filter.name,
            entity_city: this.filter.city,
            organizationtype_id: this.filter.organizationtype_id,

            limit: this.limit,
            offset: this.offset,
          },
        })
        .then((response) => {
          this.organizations.push(...response.data.organizations);
          this.hasMore = response.data.hasMore;
          this.offset += response.data.organizations.length;
          this.isLoadingOrgs = false;
        })
        .catch((error) => {
          console.log(error);
          this.generalStore.errorSnackbar("Erro ao buscar organizações");
          this.isLoadingOrgs = false;
        });
    },
    refreshOrgs() {
      this.organizations = [];
      this.offset = 0;
      this.getOrgs();
    },
    async getOrganizationTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/organizationtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.organizationtypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar(
            "Erro ao obter os tipos de organização"
          );
        });
    },
  },
  mounted() {
    this.getOrgs();
    this.getOrganizationTypes();
  },
};
</script>

