<template>
  <v-sheet class="pa-2" elevation="8" rounded="lg" width="100%">
    <v-data-table-virtual
      fixed_header
      no-data-text="Nenhuma organização encontrada"
      loading-text="Carregando organizações..."
      :headers="headers"
      :items="tableItems"
      :loading="isLoadingOrgs"
      :height="tableHeight"
    >
      <template v-slot:item="row">
        <tr>
          <td>
            <v-btn
              v-if="
                accountStore.hasRole(['Administrador'])
                  ? true
                  : row.item.organization_id == myOrg
              "
              color="info"
              variant="text"
              icon="mdi-information-outline"
              @click="openDetailDialog(row.item, false)"
            ></v-btn>
          </td>
          <td>
            {{ row.item.organization_name }}
          </td>
          <td>
            <v-chip label color="info">{{ row.item.organization_type }}</v-chip>
          </td>
          <td>
            {{ row.item.organization_phone }}
          </td>
          <td>
            {{ row.item.organization_email }}
          </td>
          <td>
            {{ row.item.organization_city }}
          </td>
          <td v-if="accountStore.hasRole(['Administrador'])">
            <v-btn
              color="warning"
              variant="text"
              icon
              @click="openDetailDialog(row.item, true)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">Editar</v-tooltip>
            </v-btn>
            <v-btn
              color="error"
              variant="text"
              icon
              @click="openDeleteDialog(row.item)"
            >
              <v-icon>mdi-delete-outline</v-icon>
              <v-tooltip activator="parent" location="bottom"
                >Remover</v-tooltip
              >
            </v-btn>
          </td>
          <td v-if="accountStore.hasRole(['Organização'])">
            <v-btn
              v-if="row.item.organization_id == myOrg"
              color="warning"
              variant="text"
              icon
              @click="openDetailDialog(row.item, true)"
            >
              <v-icon>mdi-pencil</v-icon>
              <v-tooltip activator="parent" location="bottom">Editar</v-tooltip>
            </v-btn>
          </td>
          <!-- <td v-if="accountStore.hasRole(['Produtor', 'Agente de Campo'])">
            <v-btn
              v-if="row.item.organization_id != myOrg && !myOrg"
              color="success"
              variant="text"
              icon
              @click="openSubDialog(row.item)"
            >
              <v-icon>mdi-login</v-icon>
              <v-tooltip activator="parent" location="bottom">Entrar</v-tooltip>
            </v-btn>
            <v-btn
              v-if="row.item.organization_id == myOrg"
              color="error"
              variant="text"
              icon
              @click="openUnsubDialog(row.item)"
            >
              <v-icon>mdi-logout</v-icon>
              <v-tooltip activator="parent" location="bottom">Sair</v-tooltip>
            </v-btn>
          </td> -->
        </tr>
      </template>
    </v-data-table-virtual>
    <v-dialog
      v-model="deleteDialog"
      @afterLeave="organization = null"
      width="auto"
    >
      <v-card>
        <v-card-title class="bg-error d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-delete-outline</v-icon>
          <span>Remover Organização</span></v-card-title
        >
        <v-card-text>
          <span
            >Tem a certeza que pretende remover a organização
            <b>{{ organization?.organization_name }}</b
            >?</span
          >
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="deleteDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingDelete"
            @click="deleteOrg()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="subDialog"
      @afterLeave="organization = null"
      width="auto"
    >
      <v-card>
        <v-card-title class="bg-secondary">
          Inscrição em Organização</v-card-title
        >
        <v-card-text>
          {{
            organization
              ? "Deseja se inscrever na organização " +
                organization.organization_name +
                "?"
              : ""
          }}
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="subDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingSub"
            @click="postSubscription()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog
      v-model="unsubDialog"
      @afterLeave="organization = null"
      width="auto"
    >
      <v-card>
        <v-card-title class="bg-warning">
          Desinscrição de Organização</v-card-title
        >
        <v-card-text>
          {{
            organization
              ? "Deseja sair da organização " +
                organization.organization_name +
                "?"
              : ""
          }}
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions class="d-flex justify-space-around">
          <v-btn
            color="error"
            variant="tonal"
            icon="mdi-close"
            @click="unsubDialog = false"
          ></v-btn>
          <v-btn
            color="secondary"
            variant="tonal"
            icon="mdi-check"
            :loading="isLoadingUnsub"
            @click="deleteSubscription()"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <OrgDetail
      :organization_id="organization?.organization_id"
      :editMode="editMode"
      :dialog="detailDialog"
      @update:dialog="(value) => (detailDialog = value)"
      @refresh="() => $emit('refresh')"
    ></OrgDetail>
  </v-sheet>
</template>

<script>
import OrgDetail from "@/components/Organizations/OrgDetail.vue";

import { useGeneralStore } from "@/stores/generalStore";
import { useAccountStore } from "@/stores/accountStore";

import axios from "axios";

export default {
  name: "OrganizationsTable",
  components: {
    OrgDetail,
  },
  props: {
    organizations: {
      type: Array,
      required: true,
    },
    isLoadingOrgs: {
      type: Boolean,
      required: true,
    },
    hasMore: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const generalStore = useGeneralStore();
    const accountStore = useAccountStore();
    return { generalStore, accountStore };
  },
  data() {
    return {
      detailDialog: false,
      editMode: false,
      organization: null,

      subDialog: false,
      isLoadingSub: false,

      unsubDialog: false,
      isLoadingUnsub: false,

      deleteDialog: false,
      isLoadingDelete: false,
    };
  },
  computed: {
    tableHeight() {
      return window.innerHeight - 270;
    },
    headers() {
      let headers = [
        { title: "" },
        {
          title: "Nome",
          key: "organization_name",
        },
        {
          title: "Tipo",
          key: "organizationtype_name",
        },
        {
          title: "Telefone",
          key: "organization_phone",
        },
        {
          title: "Email",
          key: "organization_email",
        },
        {
          title: "Cidade",
          key: "organization_city",
        },
      ];
      if (this.accountStore.hasRole(["Administrador", "Organização"])) {
        headers.push({ title: "" });
      }
      return headers;
    },
    tableItems() {
      return this.organizations;
    },
    myOrg() {
      return this.accountStore.account?.organization;
    },
  },
  methods: {
    loadMore(isIntersecting) {
      if (isIntersecting) {
        this.$emit("loadMore");
      }
    },
    openDetailDialog(organization, editMode) {
      this.organization = organization;
      this.editMode = editMode;
      this.detailDialog = true;
    },

    openSubDialog(organization) {
      this.organization = organization;
      this.subDialog = true;
    },

    openUnsubDialog(organization) {
      this.organization = organization;
      this.unsubDialog = true;
    },

    postSubscription() {
      this.isLoadingSub = true;
      axios
        .request({
          method: "POST",
          url: "/api/organizationmembers/subscribe",

          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            organization_id: this.organization.organization_id,
          },
        })
        .then(() => {
          this.isLoadingSub = false;
          this.generalStore.successSnackbar("Inscrição realizada com sucesso");
          this.accountStore.account.organization =
            this.organization.organization_id;
          this.subDialog = false;
          this.$emit("refresh");
        })
        .catch((err) => {
          console.log(err);
          this.isLoadingSub = false;
          this.generalStore.errorSnackbar("Erro ao realizar inscrição");
        });
    },

    deleteSubscription() {
      this.isLoadingUnsub = true;
      axios
        .request({
          method: "DELETE",
          url: "/api/organizationmembers/unsubscribe",

          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: {
            organization_id: this.organization.organization_id,
          },
        })
        .then(() => {
          this.isLoadingUnsub = false;
          this.generalStore.successSnackbar(
            "Desinscrição realizada com sucesso"
          );
          this.accountStore.account.organization = null;
          this.unsubDialog = false;
          this.$emit("refresh");
        })
        .catch((err) => {
          console.log(err);
          this.isLoadingUnsub = false;
          this.generalStore.errorSnackbar("Erro ao realizar desinscrição");
        });
    },
    openDeleteDialog(organization) {
      this.organization = organization;
      this.deleteDialog = true;
    },
    deleteOrg() {
      this.isLoadingDelete = true;
      axios
        .request({
          url: "/api/organizations/" + this.organization.organization_id,
          method: "DELETE",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then(() => {
          this.isLoadingDelete = false;
          this.generalStore.successSnackbar("Organização removida com sucesso");
          this.deleteDialog = false;
          this.$emit("refresh");
        })
        .catch((err) => {
          console.log(err);
          this.isLoadingDelete = false;
          this.generalStore.errorSnackbar(err.response.data.error, 3000);
        });
    },
  },
};
</script>

<style></style>
