<template>
  <v-dialog v-model="dialog" @afterLeave="reset()" width="auto">
    <template v-slot:activator="{ props: activatorProps }">
      <v-btn color="success" append-icon="mdi-plus" v-bind="activatorProps"
        >Nova Organização</v-btn
      >
    </template>
    <template v-slot:default="{}">
      <v-card width="600">
        <v-card-title class="bg-success">
          <span class="text-h6">{{ steps[step].title }}</span>
        </v-card-title>
        <v-window v-model="step">
          <v-window-item>
            <v-card-text>
              <v-form v-model="steps[0].formValid">
                <v-row justify="center">
                  <v-col cols="8">
                    <v-select
                      v-model="organization.organizationtype_id"
                      label="Tipo de Organização"
                      placeholder="Selecione o tipo de organização"
                      prepend-inner-icon="mdi-domain"
                      no-data-text="Nenhum tipo encontrado"
                      :items="organizationtypes"
                      item-title="organizationtype_name"
                      item-value="organizationtype_id"
                      color="secondary"
                      variant="outlined"
                      density="comfortable"
                      :loading="isLoadingTypes"
                      :disabled="isLoadingTypes"
                      :rules="[(v) => !!v || 'Campo obrigatório']"
                    ></v-select>
                  </v-col>
                </v-row>
              </v-form>
            </v-card-text>
          </v-window-item>
          <v-window-item>
            <v-card-text>
              <OrgForm
                :organization="organization"
                :formValid="steps[1].formValid"
                :readonly="false"
                @update:organization="(value) => (organization = value)"
                @update:formValid="(value) => (steps[1].formValid = value)"
              ></OrgForm>
            </v-card-text>
          </v-window-item>
        </v-window>

        <v-card-actions class="d-flex justify-space-between">
          <v-btn
            v-if="step > 0"
            prepend-icon="mdi-chevron-left"
            variant="tonal"
            @click="step--"
          >
            Anterior
          </v-btn>
          <v-spacer></v-spacer>
          <v-btn
            v-if="step < lasStep"
            :disabled="!steps[step].formValid"
            append-icon="mdi-chevron-right"
            color="secondary"
            variant="tonal"
            @click="step++"
          >
            Próximo
          </v-btn>
          <v-btn
            v-else-if="step === lasStep"
            :disabled="!steps[lasStep].formValid"
            color="success"
            variant="flat"
            append-icon="mdi-check"
            :loading="isLoading"
            @click="addOrg()"
          >
            Adicionar
          </v-btn>
        </v-card-actions>
      </v-card>
    </template>
  </v-dialog>
</template>

<script>
import OrgForm from "@/components/Organizations/OrgForm.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";

export default {
  name: "AddOrg",
  components: {
    OrgForm,
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      dialog: false,
      organization: {
        organization_name: null,
        organization_phone: null,
        organization_email: null,
        organization_address: null,
        organization_city: null,
        organizationtype_id: null,
        organization_nif: null,
      },
      isLoading: false,
      // formValid: false,

      // organization types
      organizationtypes: [],
      isLoadingTypes: false,

      step: 0,
      steps: [
        { title: "Tipo de Organização", formValid: false },
        { title: "Detalhes da Organização", formValid: false },
      ],
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    lasStep() {
      return this.steps.length - 1;
    },
  },
  methods: {
    onOpenDialog() {
      this.getOrganizationTypes();
    },
    async getOrganizationTypes() {
      this.isLoadingTypes = true;
      await axios
        .request({
          url: "api/organizationtypes",
          method: "GET",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.organizationtypes = response.data;
          this.isLoadingTypes = false;
        })
        .catch((error) => {
          console.error(error);
          this.isLoadingTypes = false;
          this.generalStore.errorSnackbar(
            "Erro ao obter os tipos de organização"
          );
        });
    },
    async addOrg() {
      this.isLoading = true;
      let payload = {
        entity_name: this.organization.organization_name,
        entity_phone: this.organization.organization_phone,
        entity_email: this.organization.organization_email,
        entity_address: this.organization.organization_address,
        entity_city: this.organization.organization_city,
        organizationtype_id: this.organization.organizationtype_id,
        organization_nif: this.organization.organization_nif,
      };
      console.log(payload);
      await axios
        .request({
          url: "api/organizations",
          method: "POST",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoading = false;
          this.generalStore.successSnackbar(
            "Organização adicionada com sucesso"
          );
          this.$emit("refresh");
          this.dialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoading = false;
          this.generalStore.errorSnackbar("Erro ao adicionar organização");
        });
    },
    reset() {
      this.isLoading = false;
      this.organization = {
        organization_name: null,
        organization_phone: null,
        organization_email: null,
        organization_address: null,
        organization_city: null,
        organizationtype_id: null,
        organization_nif: null,
      };
      this.organitiontypes = [];
      this.step = 0;
    },
  },
};
</script>

<style></style>
