<template>
  <v-dialog
    v-model="localDialog"
    @afterleave="reset()"
    :persistent="editMode"
    width="auto"
  >
    <v-card width="600">
      <v-card-title
        class="bg-info d-flex justify-space-between align-center"
        :class="{ 'bg-warning': editMode }"
      >
        <div v-if="editMode" class="d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-pencil</v-icon>
          <span>Editar Organização</span>
        </div>
        <div v-else class="d-flex align-center">
          <v-icon size="small" class="mr-2">mdi-information-outline</v-icon>
          <span>Detalhes da Organização</span>
        </div>
        <v-chip label v-show="!isLoadingGet" color="info" variant="flat">{{
          organization?.organization_type
        }}</v-chip>
      </v-card-title>
      <v-card-text>
        <v-row v-if="isLoadingGet" align="center">
          <v-col class="d-flex align-center justify-center">
            <v-progress-circular
              color="secondary"
              size="50"
              width="7"
              indeterminate
            ></v-progress-circular>
          </v-col>
        </v-row>
        <OrgForm
          v-else
          :organization="organization"
          :formValid="valid"
          :readonly="!editMode"
          @update:organization="(value) => (organization = value)"
          @update:formValid="(value) => (valid = value)"
        ></OrgForm>
      </v-card-text>
      <v-divider></v-divider>
      <v-card-actions v-if="editMode" class="d-flex justify-space-around">
        <v-btn
          color="error"
          variant="tonal"
          icon="mdi-close"
          @click="localDialog = false"
        ></v-btn>
        <v-btn
          color="secondary"
          variant="tonal"
          icon="mdi-check"
          :loading="isLoadingEdit"
          :disabled="!valid || isLoadingGet"
          @click="editOrg()"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import OrgForm from "@/components/Organizations/OrgForm.vue";

import { useAccountStore } from "@/stores/accountStore";
import { useGeneralStore } from "@/stores/generalStore";

import axios from "axios";
export default {
  name: "OrgDetail",
  components: {
    OrgForm,
  },
  props: {
    organization_id: {
      type: Number,
    },
    editMode: {
      type: Boolean,
      required: false,
      default: false,
    },
    dialog: {
      type: Boolean,
      required: true,
    },
  },
  setup() {
    const accountStore = useAccountStore();
    const generalStore = useGeneralStore();
    return { accountStore, generalStore };
  },
  data() {
    return {
      isLoadingGet: false,
      isLoadingEdit: false,
      // orgForm: {
      organization: null,
      rules: {
        required: [
          (v) => !!v || "Campo obrigatório",
          (v) => (v && v.length <= 50) || "Máximo de 50 caracteres",
        ],
        email: [
          (v) => !!v || "Campo obrigatório",
          (v) => /.+@.+\..+/.test(v) || "Email inválido",
        ],
        number: [
          (v) => !!v || "Campo obrigatório",
          (v) => (v && v.length === 9) || "Número de telefone inválido",
        ],
      },
      valid: false,
      // },
    };
  },
  watch: {
    dialog(val) {
      if (val) {
        this.onOpenDialog();
      }
    },
  },
  computed: {
    localDialog: {
      get() {
        return this.dialog;
      },
      set(val) {
        this.$emit("update:dialog", val);
      },
    },
  },
  methods: {
    async onOpenDialog() {
      await this.getOrg();
    },

    async getOrg() {
      this.isLoadingGet = true;
      // await new Promise((resolve) => setTimeout(resolve, 2000));

      await axios
        .request({
          url: "api/organizations/" + this.organization_id,
          method: "get",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
        })
        .then((response) => {
          this.organization = response.data;
          this.isLoadingGet = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingGet = false;
          this.generalStore.errorSnackbar("Erro ao obter organização");
        });
    },
    async editOrg() {
      this.isLoadingEdit = true;
      let payload = {
        organization_nif: this.organization.organization_nif,
        entity_name: this.organization.organization_name,
        entity_email: this.organization.organization_email,
        entity_phone: this.organization.organization_phone,
        entity_address: this.organization.organization_address,
        entity_city: this.organization.organization_city,
      };
      await axios
        .request({
          url: "api/organizations/" + this.organization.organization_id,
          method: "patch",
          headers: {
            Authorization: "Bearer " + this.accountStore.account.token,
          },
          data: payload,
        })
        .then((response) => {
          this.isLoadingEdit = false;
          this.generalStore.successSnackbar("Organização editada com sucesso");
          this.$emit("refresh");
          this.localDialog = false;
        })
        .catch((error) => {
          console.log(error);
          this.isLoadingEdit = false;
          this.generalStore.errorSnackbar("Erro ao editar organização");
        });
    },
    reset() {
      this.organization = null;
    },
  },
};
</script>

<style></style>
